import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  background: ${({ theme }) => theme.secondaryLight};
  color: ${({ theme }) => theme.primary};
  display: block;
  border-radius: 4px;
  margin: 16px;
  padding: 16px;
  height: 260px;
  text-transform: uppercase;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  @media (min-width: 768px) {
    width: 240px;
  }
`;
const Name = styled.div`
  width: 100%;
  font-size: 20px;
  padding: 8px 0;
  font-weight: bold;
`;
const Description = styled.div`
  padding: 8px 0;
`;

const Recommendation = styled.div`
  padding: 8px 0;
`;

const RouteBtn = styled.a`
  background: ${({ theme }) => theme.primary};
  color: ${({ theme }) => theme.white};
  cursor: pointer;
  text-align: center;
  padding: 12px;
  border-radius: 4px;
  position: absolute;
  text-decoration: none;
  bottom: 16px;
  left: 0;
  right: 0;
  margin: 0 16px;
  &:hover {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  }
`;
const Card = ({ card }) => {
  const RouteText = [
    "Route berechnen",
    "Zeige mir den Weg",
    "Bring mich dorthin",
    "Nichts wie hin!",
    "Wegbeschreibung",
  ];
  return (
    <Wrapper>
      <Name>{card.name}</Name>
      <Description>‚ÑπÔ∏è {card.info}</Description>
      {card.order && <Recommendation>üçΩ {card.order}</Recommendation>}
      <RouteBtn target="_blank" href={card.link}>
        {RouteText[Math.floor(Math.random() * RouteText.length)]}
      </RouteBtn>
    </Wrapper>
  );
};

export default Card;
